.tasks-list-wrap {
    position: relative;
    padding: 0 40px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-positive: 1;
        flex-grow: 1;
    overflow: auto;
}

.tasks-list {
    margin: 0;
    padding: 0;
    width: 100%;
    list-style-type: none;
}

.tasks-list__item:not(:last-child) {
    border-bottom: 1px solid #BDBDBD;
}



